    import service from '../../service.js';
	import mpvuePicker from '../../components/mpvue-picker/mpvuePicker.vue';
    import mInput from '../../components/m-input.vue'
	import { dlrRegister, getRegions, dlrRegisterCode } from '@/utils/api'

    export default {
        components: {
            mInput,
			mpvuePicker
        },
        data() {
            return {
                providerList: [],
                hasProvider: false,
                mobile: '',
                code: '',
				partner_id: '',
				realname: '',
				id_card: '',
                positionTop: 0,
				mulLinkageTwoPicker: [],
				pickerValueDefault:[0],
				mode: '',
				deepLength: 1,
				themeColor: '#007AFF',
				pickerValueIndex: [0, 0],
				pickerText: '',
				pickerValueArray: [],
				city_name: '',
				city: '',
				codeText: '发送验证码',
				codeTimer: null,
				newPage: 'dlr_'
            }
        },
        methods: {
			fixIos: function(){
				setTimeout(function() {                
					var scrollHeight = document.documentElement.scrollTop || document.body.scrollTop || 0;
					window.scrollTo(0, Math.max(scrollHeight - 1, 0));            
				}, 100);
			},
			// 二级联动选择
			showMulLinkageTwoPicker() {
				this.pickerValueArray = this.mulLinkageTwoPicker
				this.mode = 'multiLinkageSelector'
				this.deepLength = 2
				this.pickerValueDefault = [0, 0]
				this.$refs.mpvuePicker.show()
			},
			onConfirm(e) {
				this.city = e.value[1];
				this.city_name = e.label.split("-")[1];
				this.pickerText = e.label.split("-").join(' ');
				// this.activeText = e.index[0] == 1 ? '桂' : '陕';
			},
			onCancel(e){
				
			},
			async getCode(){
				if(this.codeText != '发送验证码' &&  this.codeText != '再次发送') return
				
				if(!/^(0|86|17951)?(13[0-9]|14[5-9]|15[012356789]|16[6]|17[0-8]|18[0-9]|19[89])[0-9]{8}$/.test(this.mobile)){
					wx.showToast({
						icon: 'none',
						title: '请输入正确的手机号',
						duration: 1000
					})
					return
				}
				
				dlrRegisterCode({mobile: this.mobile})
				wx.showToast({
					icon: 'none',
					title: '发送成功',
					duration: 1000
				})
				
				this.codeText = 30
				this.codeTimer = setInterval(()=>{
					if(this.codeText == 1) {
						this.codeText = '再次发送'
						clearInterval(this.codeTimer)
					} else {
						this.codeText == --this.codeText
					}
				}, 1000)
			},
			async bindRegister(){
				if(!/^(0|86|17951)?(13[0-9]|14[5-9]|15[012356789]|16[6]|17[0-8]|18[0-9]|19[89])[0-9]{8}$/.test(this.mobile)){
					wx.showToast({
						icon: 'none',
						title: '请输入正确的手机号',
						duration: 1000
					})
					return
				}
				
				if(!this.code.trim()){
					wx.showToast({
						icon: 'none',
						title: '请输入正确的验证码',
						duration: 1000
					})
					return
				}
				
				if(!this.city) {
					wx.showToast({
						icon: 'none',
						title: '请选择地区',
						duration: 1000
					})
					return
				}
				
				if(!this.partner_id) {
					wx.showToast({
						icon: 'none',
						title: '请输入渠道号',
						duration: 1000
					})
					return
				}
				
				if(!this.realname.trim()){
					wx.showToast({
						icon: 'none',
						title: '请输入姓名',
						duration: 1000
					})
					return
				}
				
				if(!/^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$|^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}([0-9]|X)$/.test(this.id_card)){
					wx.showToast({
						icon: 'none',
						title: '请输入正确的身份证号码',
						duration: 1000
					})
					return
				}
				let curPage = this.getCurPage();
				let curParam = curPage.options || curPage.$route.query;
				let openidParam = curParam.open_id;
				let openid = uni.getStorageSync('openid')
				let params = {
					mobile: this.mobile,
					code: this.code, 
					partner_id: this.partner_id, 
					realname: this.realname, 
					id_card: this.id_card, 
					city_name: this.city_name, 
					city: this.city,
					openid: openid || openidParam
				}
				
				let res = await dlrRegister(params)
				
				if(res.code == 200){
					let token = res.token
					if(token){
						uni.setStorageSync('token', token);
						let curPage = this.getCurPage();
						let curParam = curPage.options || curPage.$route.query;
						let openid = curParam.open_id;
						let redit_url = curParam.redit_url;
						let registerBack = uni.getStorageSync('registerBack')
						
						if(redit_url){
							let path = decodeURIComponent(redit_url).split('#')[1]
							if(path.indexOf(this.newPage) != -1){	
								uni.reLaunch({
									url: path + `?open_id=${openid}`
								})
							} else {
								uni.reLaunch({
									url: path + `?partner_id=${this.partner_id}&imei=${this.mobile}`
								})
							}
						}else if(registerBack){
							uni.reLaunch({
								url: registerBack
							})
						} else {
							uni.navigateBack()
						}
					}else {
						wx.showToast({
							icon: 'none',
							title: '注册失败',
							duration: 1000
						})
					}
				}
			},
			getCurPage(){
				let pages = getCurrentPages();
				let curPage = pages[pages.length-1];
				return curPage
			},
        },
        async onLoad() {
			let res = await getRegions()
			if(res.code == 200){
				this.mulLinkageTwoPicker = res.data
			}
        },
		onHide(){
			clearInterval(this.codeTimer)
		}
    }
